.tableBackground{width:100%;height:100%;background-Image:url(../pics/TestTable.png);position:fixed;background-position:center;background-Size:cover;-webkit-animation:fadein 2s;animation:fadein 2s;z-index:-1}.coffeeCup{border:none;background:0 0;outline:0;position:fixed;top:20%;left:73%;-webkit-animation:fadein 2s;animation:fadein 2s}.coffeeCup img{height:10vw;width:10vw}header{font-family:Ubuntu,sans-serif;padding-left:8vw;padding-top:15vh;-webkit-animation:fadein 2s;animation:fadein 2s}header h1{font-size:8vw}header h2{margin-top:10px;font-size:2vw}header .namehook a{text-decoration:none;color:#933}.type-wrap{font-size:1.3vw;font-family:Ubuntu,sans-serif;padding-left:8vw;-webkit-animation:fadein 2s;animation:fadein 2s}.mobile-wrap{display:none;font-family:Ubuntu,sans-serif}.sidebar{-webkit-animation:fadein 2s;animation:fadein 2s;z-index:9999}.sidebar ul{padding-left:8vw}.sidebar ul li{list-style-type:none;padding-top:3vh}.sidebar ul li a{text-decoration:none;font-family:Ubuntu,sans-serif;color:#000;font-size:1.3vw}.sidebar ul li a:hover{opacity:.5}@media only screen and (max-width:480px),only screen and (max-device-width:480px){.tableBackground{background-image:none}.coffeeCup img{display:none}header{padding-left:4vw;padding-top:0}header h1{font-size:50px}header h2{margin-top:10px;font-size:20px}.type-wrap{font-size:15px;padding-left:4vw;padding-bottom:10px;border-bottom:solid}.mobile-wrap{display:block;padding-left:4vw}.mobile-wrap h1{font-size:30px;color:#933;margin-bottom:5px}.mobile-wrap ul{padding-left:0}.mobile-wrap ul li{font-size:20px;color:#933;list-style:none}.mobile-wrap ul h1{margin-top:5px;font-size:15px}.mobile-wrap ul p{padding-right:4vw}.sidebar{display:none}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}


